import "./styles.css";

const app = document.getElementById("app");

function makeNav() {
  const nav = document.createElement("ul");
  const menu = ["main", "architecture", "coding", "contact", "about"];
  for (let i of menu) {
    const a = document.createElement("a");
    a.innerText = i;
    a.setAttribute("href", "#");
    a.classList.add("sumenu");
    nav.append(a);
    console.log(i);
  }
  app.append(nav);
}
makeNav();
